<template>
    <el-upload style="width: 50px" action="/common/upload/multipart/" :show-file-list="false" :on-progress="onProgress" :on-change="onChange" :on-success="onSuccess" :multiple="mode==='group'" :before-upload="beforeUpload">
        <template v-if="mode==='single'">
            <el-col :span="14" v-if="fileUrl">
                <el-popover placement="right-start" :title="fileName" width="500" trigger="hover">
                    <a :href="fileUrl" target="_blank"><img :src="fileUrl" width="500"></a>
                    <img slot="reference" :src="fileUrl" width="30" height="30">
                </el-popover>
            </el-col>
            <el-col v-else :span="4">
                <el-button slot="default" type="primary" size="mini" :disabled="isUploading">
                    <i :class="{'el-icon-upload2 el-icon--right':!isUploading,'el-icon-loading el-icon--right':isUploading}"></i>
                </el-button>
            </el-col>
        </template>
        <template v-else>
            <el-row type="flex" :gutter="48" v-for="(item,key) in value" v-bind:key="key" @click.stop="none()">
                <el-col :span="5">
                    <el-popover placement="right-start" :title="fileName[key]||item" width="500" trigger="hover">
                        <a :href="fileUrl[key]||item" target="_blank"><img :src="fileUrl[key]||item" width="500"></a>
                        <img slot="reference" :src="fileUrl[key]||item" width="30" height="30">
                    </el-popover>
                </el-col>
                <el-col :span="5">
                    <el-button @click.stop="moveUpImage(key)" :disabled="key===0" type="small"></el-button>
                </el-col>
                <el-col :span="5">
                    <el-button @click.stop="moveDownImage(key)" :disabled="key===value.length-1" type="small"></el-button>
                </el-col>
                <el-col :span="5">
                    <el-button @click.stop="removeImage(key)" type="small">x</el-button>
                </el-col>
            </el-row>
            <el-col :span="4">
                <el-button slot="default" type="primary" size="mini" :disabled="isUploading">
                    <i :class="{'el-icon-upload2 el-icon--right':!isUploading,'el-icon-loading el-icon--right':isUploading}"></i>
                </el-button>
            </el-col>
        </template>
        <!--        <i v-else slot="default" :class="{'el-icon-upload2':!isUploading,'el-icon-loading':isUploading}"></i>-->
    </el-upload>
</template>

<script>
import http from '../cloud/http'
import OSS from "ali-oss";

export default {
    name: "ImageUploader",
    props: {
        value: {},
        // single/group
        mode: {
            type: String,
            default: 'single'
        },
        // id/url
        dt: {
            type: String,
            default: 'url'
        }
    },
    mounted() {
        this.loadFile()
    },
    data() {
        return {
            isUploading: false,
            file: this.value,
            fileUrl: this.mode === 'group' ? [] : '',
            fileName: this.mode === 'group' ? [] : '',
            isLoad: false,
            isReady: false,
            client: {},
            ossFile: {
                oss_server: '',
                oss_bucket: '',
                oss_object: '',
                file_type: '',
                file_ext: '',
                file_original_name: '',
                file_size: '',
                created_scene: 3,
                object_url: ''
            },
            stsParams: {expire: 0}
        }
    },
    watch: {
        //propvaluemyValue
        value(v) {
            this.file = v
            if (!this.isLoad) {
                this.isLoad = true
            }
            this.loadFile()
        },
        // file(v) {
        //     this.$emit('input', v)
        // }
    },
    methods: {
        async auth() {
            let cache = this.$cookie.get('_IMAGE_UPLOADER_STS_PARAMS_')
            let result
            if (!cache || cache.expire <= new Date().getTime()) {
                this.isUploading = true
                try {
                    result = await http({
                        url: '/aliyun/oss/auth/',
                        method: 'get',
                        params: {}
                    })
                    this.isUploading = false
                    if (result.errCode) {
                        return this.$message.warning(',:' + result.message)
                    }
                    this.$cookie.set('_IMAGE_UPLOADER_STS_PARAMS_', {
                        result: result,
                        expire: new Date().getTime() + 10 * 60 * 1000
                    })
                } catch (error) {
                    this.isUploading = false
                    this.$message.warning('OSS,' + error)
                    return
                }
            } else {
                result = cache.result
            }
            this.ossFile.oss_server = result.data.server.server_id
            this.ossFile.oss_bucket = result.data.server.bucket
            this.ossFile.oss_object = result.data.object_path
            this.ossFile.object_url = result.data.server.cdn_domain
            this.client = new OSS({
                //yourRegionBucket1Regionoss-cn-hangzhou
                region: result.data.server.regionId,
                //STSAccessKey IDAccessKey Secret
                accessKeyId: result.data.credentials.AccessKeyId,
                accessKeySecret: result.data.credentials.AccessKeySecret,
                //STSSecurityToken
                stsToken: result.data.credentials.SecurityToken,
                refreshSTSToken: async () => {
                    //STS
                    const info = await fetch('/aliyun/oss/auth/');
                    return {
                        accessKeyId: info.data.credentials.AccessKeyId,
                        accessKeySecret: info.data.credentials.AccessKeySecret,
                        stsToken: info.data.credentials.SecurityToken,
                    }
                },
                // 
                refreshSTSTokenInterval: 10 * 60 * 1000,
                // Bucket
                bucket: result.data.server.bucket
            })
            this.isReady = true
        },
        none() {
            return false
        },
        removeImage(index) {
            this.value.splice(index, 1)
            this.fileName.splice(index, 1)
            this.fileUrl.splice(index, 1)
        },
        moveUpImage(index) {
            let thisValue = this.$copy(this.value[index])
            let upperValue = this.$copy(this.value[index - 1])
            let file = this.$copy(this.file)
            file[index] = upperValue
            file[index - 1] = thisValue
            this.file = file

            let thisFileName = this.$copy(this.fileName[index])
            let upperFileName = this.$copy(this.fileName[index - 1])
            let fileName = this.$copy(this.fileName)
            fileName[index] = upperFileName
            fileName[index - 1] = thisFileName
            this.fileName = fileName

            let thisFileurl = this.$copy(this.fileUrl[index])
            let upperFileurl = this.$copy(this.fileUrl[index - 1])
            let fileUrl = this.$copy(this.fileUrl)
            fileUrl[index] = upperFileurl
            fileUrl[index - 1] = thisFileurl
            this.fileUrl = fileUrl
        },
        moveDownImage(index) {
            let thisValue = this.$copy(this.value[index])
            let lowerValue = this.$copy(this.value[index + 1])
            let file = this.$copy(this.file)
            file[index] = lowerValue
            file[index + 1] = thisValue
            this.file = file
            let thisFileName = this.$copy(this.fileName[index])
            let lowerFileName = this.$copy(this.fileName[index + 1])
            let fileName = this.$copy(this.fileName)
            fileName[index] = lowerFileName
            fileName[index + 1] = thisFileName
            this.fileName = fileName
            let thisFileurl = this.$copy(this.fileUrl[index])
            let lowerFileurl = this.$copy(this.fileUrl[index + 1])
            let fileUrl = this.$copy(this.fileUrl)
            fileUrl[index] = lowerFileurl
            fileUrl[index + 1] = thisFileurl
            this.fileUrl = fileUrl
        },
        loadFile() {
            this.fileUrl = this.mode === 'group' ? [] : ''
            this.fileName = this.mode === 'group' ? [] : ''
            if (!this.file || !this.file.length) {
                if (this.mode === 'group') {
                    this.file = []
                }
                return
            }
            if (this.dt === 'id') {
                this.isUploading = true
                if (this.mode === 'group') {
                    this.file.forEach(id => {
                        http({
                            url: '/common/file/get/' + id + '/',
                            method: 'get',
                            params: {id: id}
                        }).then(result => {
                            this.fileName.push(result.data.original_name)
                            this.fileUrl.push(result.data.object_url)
                        }).catch(error => {
                            console.log(':' + error)
                        }).finally(() => {
                            this.isUploading = false
                        })
                    })
                } else {
                    http({
                        url: '/common/file/get/' + this.file + '/',
                        method: 'get',
                        params: {id: this.file}
                    }).then(result => {
                        this.fileName = result.data.original_name
                        this.fileUrl = result.data.object_url
                    }).catch(error => {
                        console.log(':' + error)
                    }).finally(() => {
                        this.isUploading = false
                    })
                }
            } else {
                if (this.mode === 'group') {
                    this.file.forEach(image => {
                        this.fileName.push('')
                        this.fileUrl.push(image)
                    })
                } else {
                    this.fileName = ''
                    this.fileUrl = this.file
                }
            }
        },
        beforeUpload(file) {
            this.auth().then(() => {
                const isJPG = file.type === 'image/jpeg';
                const isPNG = file.type === 'image/png';
                const isGif = file.type === 'image/gif';
                const isLt2M = file.size / 1024 / 1024 < 10;
                if (!isJPG && !isPNG && !isGif) {
                    this.$message.error(' JPG/png/gif !');
                    return false
                }
                if (!isLt2M) {
                    this.$message.error(' 10MB!');
                    return false
                }
                if (!this.isReady) {
                    this.$message.warning('')
                    return false
                }
                let ossFile = this.$copy(this.ossFile)
                ossFile.file_original_name = file.name
                ossFile.file_size = file.size
                ossFile.file_type = file.type.split("/")[0]
                ossFile.file_ext = file.type.split("/")[1]
                ossFile.oss_object = ossFile.oss_object + this.$md5((new Date()).getTime().toString()) + '.' + ossFile.file_ext
                ossFile.object_url = ossFile.object_url + ossFile.oss_object
                this.isUploading = true
                this.client.put(ossFile.oss_object, file).then(() => {
                    let data = ossFile
                    http({
                        url: '/aliyun/oss/save/',
                        method: 'post',
                        data
                    }).then(result => {
                        this.isUploading = false
                        this.onSuccess(result)
                    }).catch(error => {
                        this.$message.warning('' + error)
                    })
                }).catch(err => {
                    this.isUploading = false
                    this.$message.alert(err);
                });
            })
            return false
        },
        onChange() {
            this.isUploading = false
        },
        onProgress() {
            if (this.mode !== 'group') {
                this.fileUrl = ''
            }
            this.isUploading = true
        },
        onSuccess(result) {
            if (result.errCode) {
                return this.$message.error(result.message)
            }
            if (this.mode === 'group') {
                this.fileUrl.push(result.data.object_url)
                this.fileName.push(result.data.original_name)
                this.file.push(this.dt === 'id' ? result.data.file : result.data.object_url)
            } else {
                this.fileUrl = result.data.object_url
                this.fileName = result.data.original_name
                this.file = this.dt === 'id' ? result.data.file : result.data.object_url
            }
            this.$emit('input', this.file)
            //this.$emit('changed', this.return == 'id' ? result.data.file : result.data.object_url)
            //this.imageUrl = result.data.object_url// URL.createObjectURL(file.raw);
        },
    }
}
</script>

