<template>
    <el-container class="api" v-title data-title="">
        <css href="//at.alicdn.com/t/font_1896021_aem2yk1526s.css"/>
        <el-header class="api-header">
            <el-col :span="24">
                <h1>{{ serviceName || '' }}
                    <!--                    <i class="el-icon-refresh cursor-pointer" @click="getDocument"></i>-->
                </h1>
                <el-select v-model="service" placeholder="" class="fr" style="top: -30px" @change="selectModule">
                    <template slot="prefix">
                        <span :class="{'el-icon-refresh':!isLoading,'el-icon-loading':isLoading}" style="margin: 10px 5px 10px;cursor: pointer" @click.stop="load"></span>
                    </template>
                    <el-option
                        v-for="item in app"
                        :key="item.name"
                        :label="item.cn_name||item.name"
                        :value="item.name">
                    </el-option>
                </el-select>
            </el-col>
        </el-header>
        <el-container style="overflow: auto">
            <el-aside width="300px" class="api-aside">
                <el-menu :unique-opened="true" :default-active="defaultActive" class="el-menu-vertical-demo" :default-openeds="openeds">
                    <el-submenu v-for="(item,key) in modules" :key="key" :index="(key+1).toString()">
                        <template slot="title">
                            <i class="el-icon-ali-dian"></i>
                            <span>{{
                                    item.desc
                                }}</span>
                        </template>
                        <el-menu-item-group>
                            <template slot="title">
                                {{ item.name }}
                            </template>
                            <el-menu-item v-for="(api,akey) in item.methods" :key="akey" :index="(key+1).toString()+'-'+(akey+1).toString()" @click="detail(api,key)">
                                {{ api.desc }}
                            </el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-main v-if="!api.name">
                <el-alert title="" type="info" effect="dark" :closable="false"></el-alert>
            </el-main>
            <el-main v-if="api.name">
                <el-alert :title="':'+debugModule.name+' ::'+gateway.dev+' :'+gateway.produce+''" type="info" effect="dark" close-text="" :closable="false"></el-alert>
                <div class="mt-20">

                    <el-row class="mt-20">
                        <span>:</span>
                        <el-col :span="24">
                            <div class="description mt-15">
                                <b>{{
                                        api.path
                                    }}</b>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row class="mt-20">
                        <span>:</span>
                        <el-col :span="24">
                            <div class="description mt-15">
                                <b>{{
                                        api.method
                                    }}</b>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row class="mt-20">
                        <span>:</span>
                        <el-col :span="24">
                            <div class="description mt-15" v-html="api.explain"></div>
                        </el-col>
                    </el-row>
                    <el-tabs v-model="activeParamsTab" class="mt-15">
                        <el-tab-pane label="" name="biz">
                            <el-col :span="24">
                                <el-alert title=": {rand}:32;{timestamp}:;{appid}:appid;{sign}:" show-icon :closable="false" type="info"></el-alert>
                            </el-col>
                            <el-row class="mt-20">
                                <el-col :span="24" class="mt-10">
                                    <el-table :data="api.params" border style="width: 100%" row-key="key" default-expand-all>
                                        <el-table-column prop="key" label="" width="200">
                                            <template slot-scope="{row}">
                                                <el-input v-model="row.key" placeholder="" size="medium" v-if="row.temp==1"></el-input>
                                                <span v-if="row.temp!=1">{{
                                                        row.key
                                                    }}</span>
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="type" label="" width="150"></el-table-column>
                                        <el-table-column prop="required" label="" width="100">
                                            <template slot-scope="scope">
                                                <el-tag type="success" v-if="scope.row.required==1">
                                                    
                                                </el-tag>
                                                <el-tag type="info" v-if="scope.row.required==0">
                                                    
                                                </el-tag>
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="desc" label=""></el-table-column>
                                        <el-table-column prop="default" label="" width="100">
                                            <template slot-scope="scope">
                                        <span>{{
                                                scope.row.default || '--'
                                            }}</span>
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="value" label="" width="350">
                                            <template slot="header">
                                                <span></span>
                                            </template>
                                            <template slot-scope="scope">
                                                <el-row :gutter="10">
                                                    <el-col :span="15" v-if="scope.row.type==='array' && scope.row.type!=='ImageArray' && scope.row.key!=='images'">
                                                        <el-input v-model="scope.row.value[0]" placeholder="" size="medium"></el-input>
                                                        <el-input v-model="scope.row.value[1]" placeholder="" size="medium" class="mt-10"></el-input>
                                                    </el-col>
                                                    <el-col :span="15" v-else-if="scope.row.type==='Image'">
                                                        <image-uploader v-model="scope.row.value" dt="id"/>
                                                    </el-col>
                                                    <el-col :span="15" v-else-if="scope.row.type==='ImageUrl' || scope.row.key==='photo' || scope.row.key==='image'">
                                                        <image-uploader v-model="scope.row.value" dt="url"/>
                                                    </el-col>
                                                    <el-col :span="15" v-else-if="scope.row.key==='images'">
                                                        <image-uploader v-model="scope.row.value" mode="group"/>
                                                    </el-col>
                                                    <el-col :span="15" v-else-if="scope.row.type==='File'">
                                                        <el-upload style="width: 50px" :action="api.url" :show-file-list="false" :on-progress="onProgress" :on-change="onChange" :on-success="onSuccess" :before-upload="beforeUpload">
                                                            <el-link type="primary">
                                                                
                                                            </el-link>
                                                        </el-upload>
                                                    </el-col>
                                                    <el-col :span="15" v-else-if="scope.row.type==='ImageArray'">
                                                        <image-uploader v-model="scope.row.value[0]" dt="id"/>
                                                        <image-uploader v-model="scope.row.value[1]" dt="id"/>
                                                    </el-col>
                                                    <el-col :span="15" v-else-if="scope.row.type==='ImageUrlArray'">
                                                        <image-uploader v-model="scope.row.value[0]" dt="url"/>
                                                        <image-uploader v-model="scope.row.value[1]" dt="url"/>
                                                    </el-col>
                                                    <span v-else-if="scope.row.type==='object'">--</span>
                                                    <el-col :span="15" v-else>
                                                        <el-input v-model="scope.row.value" placeholder="" size="medium"></el-input>
                                                    </el-col>
                                                </el-row>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </el-col>
                                <el-col :span="24" class="mt-10">
                                    <el-button type="primary" class="fr" @click="debug()" :loading="isLoading">
                                        
                                    </el-button>
                                </el-col>
                            </el-row>
                        </el-tab-pane>
                        <el-tab-pane label="" name="base" v-if="project.debug_params">
                            <el-row>
                                <el-col :span="24">
                                    <el-alert title=",,,: {rand}:32;{timestamp}:;{appid}:appid;{sign}:" show-icon :closable="false" type="info"></el-alert>
                                </el-col>
                                <el-col :span="24" class="mt-10">
                                    <el-table :data="project.debug_params" border style="width: 100%">
                                        <el-table-column prop="key" label="" width="180">
                                            <template slot-scope="{row}">
                                                <el-input v-model="row.key" placeholder="" size="medium"></el-input>
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="desc" label=""></el-table-column>
                                        <el-table-column prop="value" label="">
                                            <template slot="header">
                                                <span></span>
                                                <!--                                                <el-link-->
                                                <!--                                                  type="primary"-->
                                                <!--                                                  @click="addProjectParams"-->
                                                <!--                                                  class="ml-10">-->
                                                <!--                                                    +-->
                                                <!--                                                </el-link>-->
                                            </template>
                                            <template slot-scope="scope">
                                                <el-row :gutter="10">
                                                    <el-col :span="10">
                                                        <el-input v-model="scope.row.value" placeholder="" size="medium"></el-input>
                                                    </el-col>
                                                    <!--                                                    <el-col-->
                                                    <!--                                                      :span="5"-->
                                                    <!--                                                      v-if="scope.row.temp==1">-->
                                                    <!--                                                        <el-link-->
                                                    <!--                                                          type="primary"-->
                                                    <!--                                                          @click="removeProjectParams(scope.$index)">-->
                                                    <!--                                                            -->
                                                    <!--                                                        </el-link>-->
                                                    <!--                                                    </el-col>-->
                                                </el-row>

                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </el-col>
                            </el-row>
                        </el-tab-pane>
                        <el-tab-pane label="" name="sign">
                            <el-row :gutter="20">
                                <el-col :span="4">
                                    <el-input placeholder="appid" size="small" v-model="project.debug_appid">
                                        <template slot="prepend">
                                            APPID
                                        </template>
                                    </el-input>
                                </el-col>
                                <el-col :span="6">
                                    <el-input placeholder="appid" size="small" v-model="project.debug_mpappid">
                                        <template slot="prepend">
                                            MPAPPID
                                        </template>
                                    </el-input>
                                </el-col>
                                <el-col :span="10">
                                    <el-input placeholder="appkey" size="small" v-model="project.debug_appkey">
                                        <template slot="prepend">
                                            APPKEY
                                        </template>
                                    </el-input>
                                </el-col>

                            </el-row>
                        </el-tab-pane>
                    </el-tabs>
                    <el-tabs v-model="activeResultTab" class="mt-15" v-if="requestJsonData || responseJsonData">
                        <el-tab-pane label="" name="request">
                            <el-col :span="24" v-if="responseJsonData">
                                <div class="description">
                                    <json-viewer :value="responseJsonData" :expand-depth=10 copyable expanded></json-viewer>
                                </div>
                            </el-col>
                        </el-tab-pane>
                        <el-tab-pane label="" name="response">
                            <el-col :span="24" v-if="requestJsonData">
                                <div class="description">
                                    {{
                                        debug_url
                                    }}
                                </div>
                                <div class="description mt-10">
                                    <span>Header</span>
                                    <json-viewer :value="requestHeader" :expand-depth=10 copyable expanded></json-viewer>
                                </div>
                                <div class="description mt-10">
                                    <span>Form Data</span>
                                    <json-viewer :value="requestJsonData" :expand-depth=10 copyable expanded></json-viewer>
                                </div>
                            </el-col>
                        </el-tab-pane>
                    </el-tabs>
                </div>
                <div class="mt-20">
                    <span></span>
                    <el-divider></el-divider>
                    <el-alert title=",data,errCode,message,dataobject/array/string/int(floatstring)" show-icon :closable="false" type="info"></el-alert>
                    <el-row class="mt-20">
                        <span>data</span>
                        <el-col :span="24">
                            <div class="description mt-15">
                                {{
                                    api.return.data.type
                                }}:{{
                                    api.return.data.desc
                                }}
                            </div>
                        </el-col>
                        <el-col v-if="api.return.data.fields" :span="24">
                            <el-alert title="data" show-icon :closable="false" type="info"></el-alert>
                            <el-table :data="api.return.data.fields" border style="width: 100%" row-key="_index" default-expand-all>
                                <el-table-column prop="key" label="" width="350"></el-table-column>
                                <el-table-column prop="type" label="" width="180"></el-table-column>
                                <el-table-column prop="desc" label=""></el-table-column>
                            </el-table>
                        </el-col>
                    </el-row>
                </div>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import JsonViewer from 'vue-json-viewer'
import ImageUploader from '../../components/ImageUploader.vue'
import http from '../../cloud/http'
import Sign from '../../utils/sign.js'


export default {
    name: 'Api',
    data() {
        return {
            isLoading: false,
            service: this.$route.query.service || '',
            serviceName: '',
            defaultActive: '1-1',
            modules: [],
            api: {
                params: []
            },
            debugModule: '',
            debug_url: '',
            debugParams: {},
            responseJsonData: "",
            requestJsonData: "",
            activeResultTab: 'request',
            activeParamsTab: 'biz',
            openeds: [],
            requestHeader: {},
            project: {
                debug_appid: '',
                debug_appkey: '',
                debug_mpappid: '',
                debug_params: [
                    {
                        key: 'appid',
                        value: '{appid}',
                        desc: 'APPID'
                    },
                    {
                        key: 'mpappid',
                        value: '{mpappid}',
                        desc: 'appid,'
                    },
                    {
                        key: 'rand',
                        value: '{rand}',
                        desc: ''
                    },
                    {
                        key: 'timestamp',
                        value: '{timestamp}',
                        desc: ''
                    },
                    {
                        key: 'sign',
                        value: '{sign}',
                        desc: '',
                    },
                    {
                        key: 'token',
                        value: this.$cookie.get('ws_debug_token') || '',
                        desc: ''
                    },
                    {
                        key: 'sessionkey',
                        value: this.$cookie.get('ws_debug_sessionkey') || '',
                        desc: 'sessionkey'
                    },
                    {
                        key: 'client',
                        value: this.$cookie.get('ws_debug_client') || 'debug',
                        desc: '[ios;xiaomi;...]'
                    },
                    {
                        key: 'version',
                        value: this.$cookie.get('ws_debug_version') || '1.0.1',
                        desc: ''
                    }
                ]
            },
            gateway: {
                dev: '',
                produce: ''
            },
            app: []
        }
    },
    watch: {
        service() {
            if (!this.service) {
                return
            }
            this.getServiceName()
            //this.getDocument()
        }
    },
    mounted() {
        this.load()
    },
    components: {
        JsonViewer,
        ImageUploader
    },

    methods: {
        load() {
            this.$request.get('/ws/document?action=init').then(result => {
                if (result.errCode) {
                    return this.$message.error(result.message)
                }
                this.project.debug_appid = result.data.debug_appid
                this.project.debug_appkey = result.data.debug_appkey
                this.project.debug_mpappid = result.data.debug_mpappid
                this.app = result.data.modules
                this.gateway = result.data.gate_way
                if (this.app.length) {
                    this.service = this.service || this.app[0].name
                    this.getDocument()
                }
            }).catch(error => {
                this.$message.error(error)
            }).finally(() => {
                this.$loading.close();
            })
        },
        beforeUpload() {

        },
        onChange() {

        },
        onProgress() {
            this.$loading.show('')
        },
        onSuccess(result) {
            this.$loading.close()
            if (result.errCode) {
                return this.$message.alert(':' + result.message)
            }
            this.$message.success('')
            this.responseJsonData = result
        },
        selectModule() {
            this.getDocument()
        },
        getServiceName() {
            this.app.forEach(item => {
                if (item.name === this.service) {
                    this.serviceName = item.cn_name || item.name
                }
            })
        },
        debug() {
            this.debugParamsInit()
            this.debug_url = this.api.path
            let requestParams = this.debugParams
            this.requestHeader = {
                appid: requestParams.appid,
                timestamp: requestParams.timestamp,
                rand: requestParams.rand,
                sign: requestParams.sign,
                client: requestParams.client,
                version: requestParams.version
            }
            requestParams.token ? this.requestHeader.token = requestParams.token : ''
            requestParams.mpappid ? this.requestHeader.mpappid = requestParams.mpappid : ''
            requestParams.sessionkey ? this.requestHeader.sessionkey = requestParams.sessionkey : ''
            requestParams.project ? this.requestHeader.project = requestParams.project : ''
            requestParams.client ? this.requestHeader.client = requestParams.client : ''
            requestParams.version ? this.requestHeader.version = requestParams.version : ''
            delete requestParams.appid
            delete requestParams.timestamp
            delete requestParams.rand
            delete requestParams.sign
            delete requestParams.token
            delete requestParams.mpappid
            delete requestParams.sessionkey
            delete requestParams.project
            delete requestParams.client
            delete requestParams.version
            this.requestJsonData = requestParams
            if (this.api.method.toLowerCase() === 'get') {
                this.isLoading = true
                http({
                    url: this.debug_url,
                    debug: true,
                    method: 'get',
                    params: requestParams,
                    headers: this.requestHeader
                }).then(result => {
                    this.responseJsonData = result
                }).catch(error => {
                    this.$message.error(error)
                }).finally(() => {
                    this.isLoading = false
                })
            } else {
                this.isLoading = true
                let data = requestParams
                http({
                    url: this.debug_url,
                    debug: true,
                    method: 'post',
                    headers: this.requestHeader,
                    data
                }).then(result => {
                    this.responseJsonData = result
                }).catch(error => {
                    this.$message.error(error)
                }).finally(() => {
                    this.isLoading = false
                })
            }
        },
        /**
         * 
         */
        debugParamsInit() {
            this.responseJsonData = ""
            this.debugParams = {}
            let allParams = this.$copy(this.api.params)
            this.project.debug_params.forEach(item => {
                if (item.key === 'token' && item.value) {
                    this.$cookie.set('ws_debug_token', item.value)
                }
                if (item.key === 'sessionkey' && item.value) {
                    this.$cookie.set('ws_debug_sessionkey', item.value)
                }
                allParams.push(item)
            })
            this.paramsTransform(allParams)
        },
        /**
         * 
         * @param params
         * @returns {ElMessageComponent}
         */
        paramsTransform(params) {
            let rand = Sign.rand()
            let timestamp = Sign.timestamp()
            params.forEach(item => {
                if (item.value) {
                    this.$session.set('_api_debug_params_' + this.project.id + '_' + item.key, item.value)
                }
                if (item.value === '{appid}') {
                    this.debugParams[item.key] = this.project.debug_appid
                } else if (item.value === '{mpappid}') {
                    this.debugParams[item.key] = this.project.debug_mpappid
                } else if (item.value === '{rand}') {
                    this.debugParams[item.key] = rand
                } else if (item.value === '{timestamp}') {
                    this.debugParams[item.key] = timestamp
                } else if (item.value === '{token}') {
                    this.debugParams[item.key] = this.project.debug_token || ''
                } else if (item.value === '{sign}') {
                    //sign=md5(appid+md5(appkey)++)
                    this.debugParams[item.key] = this.$md5(this.project.debug_appid + this.$md5(this.project.debug_appkey) + timestamp + rand)
                } else if (item.type === 'object' && item.children) {
                    this.debugParams[item.key] = {}
                    item.children.forEach(children => {
                        this.debugParams[item.key][children.key] = children.value
                    })
                } else if (item.type === 'timestamp' && item.value && item.value.toString().length !== 10) {
                    this.debugParams[item.key] = item.value / 1000
                } else {
                    this.debugParams[item.key] = item.value || ''
                }
            })
        },
        //
        getDocument() {
            if (!this.service) {
                return this.$message.error('');
            }
            this.$loading.show('');
            this.isLoading = true
            this.$request.get('/ws/document?action=document&module=' + this.service).then(result => {
                if (result.errCode) {
                    return this.$message.alert(result.message)
                }
                this.modules = result.data
                if (this.modules.length) {
                    this.detail(this.modules[0].methods[0], 0)
                }

                // if (!this.api.name && this.service) {
                //     this.modules.forEach((v, k) => {
                //         if (v.name === this.service) {
                //             this.defaultActive = (k + 1) + "-1";
                //             this.detail(this.modules[k].methods[0], k)
                //         }
                //     })
                // } else {
                //     this.detail(this.modules[0].methods[0], 0)
                // }
            }).catch(error => {
                this.$message.error(error)
            }).finally(() => {
                this.isLoading = false
                this.$loading.close();
            })
        },
        detail(api, moduleKey) {
            this.api = this.$copy(api)
            this.debugModule = this.modules[moduleKey]
            this.api.updated = this.debugModule.updated
            this.api.author = this.debugModule.author
            this.requestJsonData = null
            this.responseJsonData = null
            this.setIndex(this.api.return.data.fields)

            if (this.api.params) {
                let params = this.api.params
                params.forEach((item, key) => {
                    this.$set(this.api.params[key], 'value', '')
                    if (item.type === 'array') {
                        this.$set(this.api.params[key], 'value', [])
                    }
                    if (item.children !== undefined) {
                        item.children.forEach((children, ckey) => {
                            this.$set(this.api.params[key].children[ckey], 'value', '')
                            if (children.type === 'array') {
                                this.$set(this.api.params[key].children[ckey], 'value', [])
                            }
                        })
                    }
                    // if (item.default) {
                    //     this.$set(this.api.params[key], 'value', item.default)
                    // }
                })
            }
            this.$nextTick(() => {
                this.scrollTop(document.querySelector(".el-main").scrollTop)
            });
        },
        scrollTop(top) {
            setTimeout(() => {
                document.querySelector(".el-main").scrollTop = top;
                top = top - 20
                if (top > 0) {
                    this.scrollTop(top)
                } else if (top < 0) {
                    document.querySelector(".el-main").scrollTop = 0;
                }
            }, 1)
        },
        setIndex(params) {
            params.forEach(item => {
                item._index = Sign.rand()
                if (item.children) {
                    this.setIndex(item.children)
                }
            })
        }
    },
}
</script>
<style>
#app {
    width: 100%;
    height: 100%;

}

.api-header {
    text-align: center;
    padding-top: 10px;
    border-bottom: 1px solid #ebebeb;
    border-radius: 3px;
    background-color: #F8F8F8;
}

.api-aside {
    padding-left: 50px;
}

.description {
    padding: 20px;
    box-sizing: border-box;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    font-size: 14px;
    line-height: 22px;
    color: #666;
    word-break: break-word;
    margin: 10px;
    background-color: #F8F8F8;
}

.jv-container, .jv-code {
    background-color: #F8F8F8;
}
</style>